import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

const style = {
  position: 'absolute' ,
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function Modal1(props) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
let tec = []

    for (let i=0 ; i <props.tech.length ; i++){
       tec.push(<li>{props.tech[i]}</li>)
    }
    
  return (
    <div>
    
      <Button variant="outlined" id='knowmore' onClick={handleOpen}>Know more</Button>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        slots={{ backdrop: Backdrop }}
        slotProps={{
          backdrop: {
            timeout: 500,
          },
        }}
      >
        <Fade in={open}>
          <Box sx={style}>
            <Typography id="transition-modal-title" variant="h6" component="h2">
              {props.title}
            </Typography>
            <Typography id="transition-modal-description" sx={{ mt: 2 }}>
              {props.description}
            </Typography>
            <Typography id="transition-modal-description" sx={{ mt: 2 }}>
            Technologies Used 
            </Typography>
            <ul>
                {tec}
            </ul>
            <Button className='modalbtn' color="success" variant="contained"> <a href={props.path} target='_blank'>Code</a></Button>
            <Button className='modalbtn' color="error" variant="contained" onClick={handleClose}>Close</Button>
          </Box>
        </Fade>
      </Modal>
    </div>
  );
}